import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
import * as mutations from './mutations'
import * as actions from './actions'
import * as getters from './getters'

const state = {
    count:0
}
const store = new Vuex.Store({
    state,
    mutations,
    actions,
    getters
});


export default store;


